.card{
    width: 240px;
    height:fit-content;
    margin: 10px;
    /* border: solid red 2px; */
    overflow: auto;
    border-radius: 5px;
}

.card img{
    width: 240px;
    height:200px;
    object-fit: cover;
    border-radius: 5px;
}
.chanel{
    display: flex;
    align-items: center;
    text-decoration: none;
    color: black;
}

.cardavatar{
    margin-right: 4px;
}

.cardbottom{
    display: flex;
}

.muted{
    color: gray;
}

.chanel{
    text-decoration: none;
    color: black;
    text-decoration-style: none;
    font-weight: 300;
}

.title{
    font-weight: bold;
}

@media (min-width:100px) and (max-width:715px) {
    .card{
        width: 340px;
        height:320px;
        margin-left: 13px;
        /* border: solid red 2px; */
        overflow: auto;
        border-radius: 5px;
    }
    
    .card img{
        width: 340px;
        height:200px;
        border-radius: 5px;
    }
  }