.header{
    display: flex;
    align-items: center;
    /* justify-content: space-evenly; */
    padding-top: 7px;
    padding-bottom: 7px;
    position: fixed;
    top: 0;
    z-index: 100;
    background-color: white;
    font-style: none;
    text-decoration: none;
    width: 100%;
  }
  
  .centre{
    display: flex;
    align-items: center;
  }
  
  .headerfirst{
    display: flex;
    justify-content: space-between;
    width: 17%;
    /* border: solid 2px red; */
    cursor: pointer;
    margin-left: 20px;
  }
  
  .headermiddle{
    width: 180%;
    min-width: 30px;
    display: flex;
    justify-content: center;
    overflow: auto;
    /* border: solid 2px red; */
    
}

input{
    width: 500px;
    padding-left: 10px;
    font-size: large;
    display: flex;
    justify-content: center;
    border-radius: 20px;
    border: none;
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px;
    
}

.headerend{
    /* border: solid 2px red; */
    /* overflow: auto; */
    width: 30%;
    display: flex;
    justify-content: space-between;
    margin-right: 5px;
  }

  .avatar{
    cursor: pointer;
  }

.logo{
    object-fit: contain;
    margin-left: 10px;
}

.border{
    display: flex;
    border: solid black 1px;
    border-radius: 20px;
}

.seacrhicon{
    border-left: 1px solid black;
    padding: 10px 20px;
    background-color: rgb(212, 210, 210);
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}

.headerendicon{
  margin-left: 10px;
}

