  input{
    width: 200px;
    display: flex;
    justify-content: center;
    border-radius: 20px;
    border: none;
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px;
    
}
}

@media (min-width:300px) and (max-width:600px) {
  input{
    width: 50px;
    display: flex;
    justify-content: center;
    border-radius: 20px;
    border: none;
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px;
    
}
}

