.row{
    display: flex;
    align-items: center;
    width: 150px;
    margin: 7px 0px;
}

.row:hover{
    background-color: lightgrey;
    border-radius: 10px;
}

.row:hover > .icon{
    color: red;
}

.icon{
    margin: 3px 10px;
    font-size: large !important;
}

.selected{
    background-color: lightgrey;
    border-radius: 10px;
}

.iconright{
    text-decoration: none;
    color: black;
}


@media (min-width:600px) and (max-width:1100px) {
    .row{
        display: flex;
        align-items: center;
        width: 150px;
        margin: 7px 0px;
    }
  }

@media (min-width:100px) and (max-width:720px) {
    .row{
        display: none;
        display: flex;
        align-items: center;
        width: 40px;
        margin: 7px 0px;
    }
    .hide{
        display: none;
    }
    
    .icon{
        margin: 3px 10px;
        display: none;
        font-size: large !important;
    }
    
    .selected{
        display: none;
        background-color: lightgrey;
        border-radius: 10px;
    }
    
    .iconright{
        display: none;
        text-decoration: none;
        color: black;
    }
  }